import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IPedidoDePresupuesto } from '../models/pedido-de-presupuesto';
import { AuthService } from '@core/auth/auth.service';
import { Response } from '@shared/models/response-with-token';
import { IProveedor } from 'app/modules/administracion/proveedores/models/proveedor';

@Injectable()
export class PedidosDePresupuestoService {
    constructor(private http: HttpClient, private _authService:AuthService) { }
    /**
     * Funcion en el servicio
     * - Esta funcion retorna los Rubros.
     * @returns Rubros
     */
    getPedidosDePresupuesto(sort?:any): Observable<Response[]> {
        let f = '?';
        f += sort == 'desc' ? 'sort=-descripcion' : 'sort=descripcion';
        let subject = new Subject<any>();

        this.http.get<Response[]>(`${environment.administracion.view_clientes}${f}`)
        .subscribe((resp:any) => {
            this._authService.accessToken = resp.token,
            subject.next({
                'list':resp.result,
                // 'totalPages': resp.headers.get('x-pagination-page-count'),
                // 'totalCount': resp.headers.get('x-pagination-total-count')
            })
        });
        return subject.asObservable();
    }

    /**
     * Funcion en el Servicio 
     * @param medio_de_pago 
     * @returns Crea un item nuevo
     */
    savePedidosDePresupuesto(medio_de_pago: IPedidoDePresupuesto): Observable<IPedidoDePresupuesto> {
        return this.http.post<IPedidoDePresupuesto>(``, medio_de_pago);
    }

    /**
     * Funcion en el servicio
     * @param pedido_de_presupuesto
     * @returns Item actualizado actualizado
     */
    updatePedidosDePresupuesto(pedido_de_presupuesto: IPedidoDePresupuesto): Observable<IPedidoDePresupuesto> {
        return this.http.put<IPedidoDePresupuesto>(`` + '/' + pedido_de_presupuesto.idpedido, pedido_de_presupuesto);
    }

    /**
     * Funcion en el servicio
     * @param idpedido 
     * @returns Elimina un item
     */
    deletePedidosDePresupuesto(idpedido: number): Observable<IPedidoDePresupuesto> {
        return this.http.delete<IPedidoDePresupuesto>(`` + '/' + idpedido);
    }

    getProveedores(): Observable<IProveedor[]> {
        let f = '?filter[activo]=1&sort=RazonSocial';
        return this.http.get<IProveedor[]>(`${environment.administracion.proveedores}${f}`);
    }
}